import React from 'react'

const AdminNav = ({ changeSection }) => {
    return (
        
            <ul>
                <li id="0" onClick={changeSection}>Dodaj produkt</li>
                <li id={1} onClick={changeSection}>Zmie produkt</li>
                <li id={2} onClick={changeSection}>Usu produkt</li>
                <li id={3} onClick={changeSection}>Kategorie</li>
                <li id={4} onClick={changeSection}>Min. warto zamwienia</li>
                <li id={5} onClick={changeSection}>Dowz</li>
                <li id={6} onClick={changeSection}>Min. wartoci gratis</li>
            </ul>
        
    )
}

export default AdminNav
